* {
    box-sizing: border-box;
}
img:hover {
    border-radius: 10px;
    box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}
div.project {
    box-sizing: border-box;
    display: block;
    padding: 10px;
}

div.sides {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
}

div.p_right {
    box-sizing: border-box;
    flex: 65%;
    float: right;
    position: relative;
}

div.p_left {
    box-sizing: border-box;
    flex: 5%;
    float: left;
} 

div.jemsPage {
    box-sizing: border-box;
    position: absolute;
    left: 2%;
    display: block;
}

#tips {
    box-sizing: border-box;
    margin-left: 0;
    width: 400px;
    height: 100%;
    position: relative;
}

#management {
    box-sizing: border-box;
    margin-left: 0;
    width: 400px;
    height: 300px;
    padding: 15px;
    position: relative;
}

div.Championship {
    box-sizing: border-box;
    position: absolute;
    left: 2%;
}